<h1>Button 按钮</h1>
<hr>
<h3>基础用法</h3>
<p>使用<code v-pre="" class="code_inline">type</code>、<code v-pre="" class="code_inline">plain</code>和<code v-pre="" class="code_inline">round</code>属性来定义 Button 的样式。</p>
