    <lu-button type="warning">警告按钮</lu-button>
    <lu-button type="danger">危险按钮</lu-button>
  </div>
  <div class="m-10">
    <lu-button plain="">朴素按钮</lu-button>
    <lu-button type="primary" plain="">主要按钮</lu-button>
