<h3>图标按钮</h3>
<p>带图标的按钮可增强辨识度（有文字）或节省空间（无文字）。
设置<code v-pre="" class="code_inline">icon</code>属性即可，icon 的列表可以参考 VV-UI 的 icon 组件，也可以设置在文字右边的 icon ，只要使用<code v-pre="" class="code_inline">i</code>标签即可，可以使用自定义图标。</p>
<div class="demo-block">
  <lu-button icon="lu-icon-tag" type="primary"></lu-button>
  <lu-button icon="lu-icon-close" type="primary"></lu-button>
  <lu-button icon="lu-icon-smile" type="primary"></lu-button>
  <lu-button icon="lu-icon-search" type="primary">搜索</lu-button>
  <lu-button icon="lu-icon-cloudfill" type="primary">下载</lu-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><lu-button icon="lu-icon-tag" type="primary"></lu-button>
<lu-button icon="lu-icon-close" type="primary"></lu-button>
<lu-button icon="lu-icon-smile" type="primary"></lu-button>
<lu-button icon="lu-icon-search" type="primary">搜索</lu-button>
<lu-button icon="lu-icon-cloudfill" type="primary">下载</lu-button>

</body></html></div>
