<h3>不同尺寸</h3>
<p>Button 组件提供除了默认值以外的三种尺寸，可以在不同场景下选择合适的按钮尺寸。
额外的尺寸：<code v-pre="" class="code_inline">medium</code>、<code v-pre="" class="code_inline">small</code>，通过设置<code v-pre="" class="code_inline">size</code>属性来配置它们。</p>
<div class="demo-block">
  <lu-button>默认尺寸</lu-button>
  <lu-button size="medium">中等按钮</lu-button>
  <lu-button size="small">小型按钮</lu-button>
</div>
<demo-block>
